import React, {Component} from 'react';
import {Table} from 'semantic-ui-react'
import color from '../colors';
import NotesModal from './NotesModal';

const timeoutLength = 2500

class Item extends Component {

    wasModifiedToday = (item,e) => {
        // console.log(item.timestamp);
        return ((new Date()).toDateString() === (new Date(item.timestamp)).toDateString());
    }

    stopPropagation = (e) => {
        e.stopPropagation();
    }

    render() {
        const props = this.props;
        return (
            <Table.Row style={{backgroundColor: (props.item.name === props.selectedItem.name)?'black':color(props.item.tags)}} 
                onClick={props.rowClickEvent} 
            >
                {/* <NotesModal {...props}
                    trigger={ */}
                        <Table.Cell width={2} textAlign="center"
                            style={{margin:0, padding:0}}
                        >
                            <strong
                                style={{margin:0, padding:0}}
                            ><i>{(this.wasModifiedToday(props.item)?"✓ ":"")}</i></strong>
                        </Table.Cell>
                    {/* }
                /> */}
                <Table.Cell width={11}> 
                    {props.item.name} <strong >{props.item.marked || props.item.notes ? " *" : ""}</strong>
                </Table.Cell>
                {/* <Table.Cell width={5}> {props.item.tags}</Table.Cell> */}
                {/* <Table.Cell style={{fontFamily:"Roboto Black"}}  textAlign="right" width={1}> {props.item.marked ? "*" : ""}</Table.Cell> */}
                <Table.Cell width={4}>
                    { props.item.qty + " " + props.item.unit}
                </Table.Cell>
            </Table.Row>
        )
    }
}

export default Item;




