import React, { Component } from 'react';
import {TransitionablePortal, Segment, Input} from 'semantic-ui-react';
import InputRange from 'react-input-range';
class RangeSlider extends Component {
  state={value:0}
  onChange = (e) => {
    console.log(e.target.value);
    this.setState({value:e.target.value});
  }
  render() {
    return (
      <div className="RangeSlider">
        <TransitionablePortal open={true}>
            <Segment inverted style={{position:'fixed', bottom:0, left:0, width:'50px', height:'100%'}}>
<input type="range" id="range" max='50' step='1'
  value={this.props.value}
  onChange={this.props.onChange}
/>
            </Segment>
        </TransitionablePortal>
      </div>
    );
  }
}

export default RangeSlider;



