import React, { Component } from 'react';
import {
    Button,
    List,
    Grid,
    Icon,
    Segment,
    TransitionablePortal
} from 'semantic-ui-react';
import color from '../colors';
// import HoldButton from './HoldButton'
import PopupNotes from './PopupNotes'

class Keypad extends Component {
    state = {keepKeypadOpen :false};

    keepKeypadOpen = () => {
        this.setState({keepKeypadOpen:!this.state.keepKeypadOpen})
      }
    
    stopPropagation = (e) => {
      // if (!this.state.keepKeypadOpen)
        e.stopPropagation();
    }

  render() {
    return (
      <div>
        <TransitionablePortal 
            open={this.props.open} 
            transition={{animation:'fade up', duration:300}} 
            // closeOnDocumentClick={!this.state.keepKeypadOpen} 
            closeOnDocumentClick={false}
            onClose={this.props.onClose}
        >
        {/* <div style={{ backgroundColor: 'transparent', left: '0%', padding:0, position: 'fixed', top: '54px', zIndex: 5001, width:"100%", height:"100%", borderRadius:0}}
          onClick={this.props.onClose}
          // onClick={this.state.keepKeypadOpen? null: this.props.onClose}
          > */}
          <Segment textAlign="center" color="pink"  style={{ backgroundColor: '#3A3A3A', left: '0%', padding:0, position: 'fixed', bottom: '0px', zIndex: 5001, width:"100%", height:"auto", borderRadius:0}}
            onClick={this.stopPropagation}
          >
            <List selection celled inverted  horizontal >
              <List.Item style={{padding:'20px 10px'}} onClick={this.props.unitClicked}>bags</List.Item>
              <List.Item style={{padding:'20px 10px'}} onClick={this.props.unitClicked}>boxes</List.Item>
              <List.Item style={{padding:'20px 10px'}} onClick={this.props.unitClicked}>trays</List.Item>
              <List.Item style={{padding:'20px 10px'}} onClick={this.props.unitClicked}>tubs</List.Item>
              <List.Item style={{padding:'20px 10px'}} onClick={this.props.unitClicked}>bin</List.Item>
              <List.Item style={{padding:'20px 10px'}} onClick={this.props.unitClicked}>shelf</List.Item>
              <List.Item style={{padding:'20px 10px'}} onClick={this.props.unitClicked}>pcs</List.Item>              
            </List>
            {/* <Segment inverted basic className="ui black" style={{borderRadius:0}}> */}
              <Button.Group size="big" compact widths="4" >
                <Button className="ui black button" onClick={this.props.numberClicked} style={{margin:0, borderRadius:"0em"}}>1</Button>
                <Button className="ui black button" onClick={this.props.numberClicked} style={{margin:0, borderRadius:"0em"}}>2</Button>
                <Button className="ui black button" onClick={this.props.numberClicked} style={{margin:0, borderRadius:"0em"}}>3</Button>
                <Button className="ui black button" onClick={this.props.numberClicked} style={{margin:0, borderRadius:"0em"}}>¼</Button>
              </Button.Group>
              <Button.Group size="big" compact widths="4" >
                <Button className="ui black button" onClick={this.props.numberClicked} style={{margin:0, borderRadius:"0em"}}>4</Button>
                <Button className="ui black button" onClick={this.props.numberClicked} style={{margin:0, borderRadius:"0em"}}>5</Button>
                <Button className="ui black button" onClick={this.props.numberClicked} style={{margin:0, borderRadius:"0em"}}>6</Button>
                <Button className="ui black button" onClick={this.props.numberClicked} style={{margin:0, borderRadius:"0em"}}>½</Button>
              </Button.Group>
              <Button.Group size="big" compact widths="4" >
                <Button className="ui black button" onClick={this.props.numberClicked} style={{margin:0, borderRadius:"0em"}}>7</Button>
                <Button className="ui black button" onClick={this.props.numberClicked} style={{margin:0, borderRadius:"0em"}}>8</Button>
                <Button className="ui black button" onClick={this.props.numberClicked} style={{margin:0, borderRadius:"0em"}}>9</Button>
                <Button className="ui black button" onClick={this.props.numberClicked} style={{margin:0, borderRadius:"0em"}}>¾</Button>
              </Button.Group>
              <Button.Group size="big" widths="4" >
                <Button className="ui black button" icon="asterisk" onClick={this.props.asteriskClicked} style={{margin:0, borderRadius:"0em"}}></Button>
                <Button className="ui black button" onClick={this.props.numberClicked} style={{margin:0, borderRadius:"0em"}}>0</Button>
                <Button className="ui black button" icon="delete" onClick={this.props.deleteClicked} style={{margin:0, borderRadius:"0em"}}></Button>
                <Button className="ui black button" icon="chevron down" onClick={this.props.onClose} style={{margin:0, borderRadius:"0em"}}/>
              </Button.Group>
            {/* </Segment> */}
          </Segment>
          {/* </div> */}
        </TransitionablePortal>
      </div>
    );
  }
}

export default Keypad;



